import axios from 'axios';

const baseUrl = '/data/events/';

const buildUrl = (year, month) => {
  return baseUrl + year + "/" + month + "/events.json";
};

export default {
  get(year, month) {
    return axios.get(buildUrl(year, month))
      .then(value => value.data)
      .catch(e => {
        console.warn(e);
      })
  }
}
