<template>
  <div class="v-calendar">
    <v-calendar :attributes='attributes'
                @update:fromPage='pageChanged'
                @update:toPage='pageChanged'
                is-expanded>
      <div slot='event'
           slot-scope='{ customData }'
           class='popover-row'>
        <span class='popover-content'>
          <a :href='customData.event.url'>{{ customData.event.title }}</a>
        </span>
        <span v-if="!customData.end">(od: {{ customData.start | dateFormat('H:mm') }})</span>
      </div>
    </v-calendar>
  </div>
</template>

<script>
  import CalendarBean from './CalendarBean';
  import cache from '../utils/cache';
  import 'v-calendar/lib/v-calendar.min.css';

  export default {
    data() {
      return {
        events: []
      };
    },
    computed: {
      attributes() {
        return [
          // Today
          {
            contentStyle: {
              fontWeight: '800',
              color: '#66b3cc',
            },
            dates: new Date(),
          },
          // Attributes for todos
          ...this.events
        ]
      }
    },
    mounted() {
      this.$nextTick(function () {
        const now = new Date();
        this.fetchEvents(now.getFullYear(), now.getMonth() + 1)
      })
    },
    methods: {
      pageChanged(page) {
        this.fetchEvents(page.year, page.month);
      },
      fetchEvents(year, month) {
        cache(`${year}.${month}`, () => {
          CalendarBean.get(year, month)
            .then(events => {
              if (events) {
                events.forEach((event) => {
                  this.events.push(event)
                });
              }
            });
            return true;
        });
      }
    }
  };
</script>
